// Variables
@import "variables";

body{
	font-size: 14px;
	background: #F2F4F5;
}

.background{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background:
	50% 50% no-repeat;
	 -webkit-background-size: cover;
     -moz-background-size: cover;
     -o-background-size: cover;
     background-size: cover;
}

.header .background{
	background-image: url("../img/bg.jpg");
	height: 100%;
	z-index: -1;
}

.header-text{
	color: #ffffff;
}

.jumbotron {
 padding:0 !important;
 margin:0;
 color: #777777;
 background: #F2F4F5;
}

.jumbotron h1 {
 color: #fa5950;
 font-size: 27px;
}

.section-padding {
 padding-top: 520px;
}

.grey {
 color:#777777;
}

.showcase-box{
	height: 230px;
	padding: 2px;
}

.showcase-item{
	color: #777777;
	padding: 20px;
	height: 100%;
	width: 100%;
}

.bottom-menu {
background: #e2e2e2;
color: #bcc3ca;
padding: 39px 0 42px;
margin: 150px 0 0 0;
}
.bottom-menu .navbar-brand {
 font-size: 20px;
 padding: 0;
}
.bottom-menu .title {
font-size: 13px;
font-weight: 700;
margin-top: 0;
}
.bottom-menu a {
color: inherit;
}
.active .bottom-menu a, .bottom-menu a:hover .bottom-menu a:focus
{
color: #e47054;
text-decoration: none;
}
.bottom-menu .bottom-links{
font-size: 14px;
line-height: 1.286;
}
.bottom-links{
margin: 0;
padding: 0;
list-style: none;
}
.bottom-links li{
display: block;
float: left;
margin: 0 20px 0 0;
}

.well-pet{
	color: #777777;
	height: 2000px;
	width: 100%;
}

.big-padding{
	padding: 30px;
}

.blue-grey{
	background-color: #37474f;
}

.white-text{
	color: white;
}

.table{
	background-color: white;
}

.navbar{
	margin-bottom: 0px;
}


